.p-footer-menu--pc {
  
  @include mq(tab) {
   display: flex;  
  }
}

.p-footer-menu--pc__item {
   padding-top: rem(4);
   padding-bottom: rem(2);
   
   @include mq(tab) {
     padding: 0;
     padding-bottom: rem(4);
     margin-left: rem(15);
     margin-right: rem(15);

     &:first-child {
       margin-left: 0;
     }
     
  }

  }
  
  .p-footer-menu--pc__item a{
    display: inline-block;
    width: 100%;
    height: 100%;
    letter-spacing: 0.125em;
    position: relative;
    text-decoration: none;
}

.p-footer-menu--pc__item a::after {
  @include mq(tab) {
    
    position: absolute;
    bottom: 0;
    left: 0;
    content: '';
    width: 100%;
    height: 1px;
    background: $category-color;
    transform: scale(0, 1);
    transform-origin: left top;
    transition: transform .3s;
  }
}
.p-footer-menu--pc__item a:hover::after {
  transform: scale(1, 1);
}